<template>
	<view class="value">
		<view class="value_item pad-30 bg-fff mar-b-10 flex-lr" v-for="item in list" :key='item.comment_id'>
			<image :src="item.is_anonymous==2?'../../static/img/avatar.png':item.avatar" class="avatar" ></image>
			<view class="flex-grow">
				<view class="flex-lr flex-center">
					<view class="">
						<view class="f-28 color-1">{{item.is_anonymous==2?'匿名':item.nickname}}</view>
						<view class="f-24 color-9 mar-t-10">{{item.create_time}}</view>
					</view>
					<view class="flex-center">
						<image class="mar-r-10 star" :src="n<item.quality_grade?'../../static/img/icon_star2.png':'../../static/img/icon_star.png'" v-for="n in 5" :key='n'></image>
					</view>
				</view>
				<text class="block color-1 f-28 mar-t-20 line-h-16">{{item.text}}</text>
				<view class="flex-lr mar-t-20"  v-if="item.label.length>0">
					<image src="../../static/img/icon_label.png" class="icon_label"></image>
					<view class="flex-grow f-24 color-6 mar-l-10 line-h-14">
						<text>{{item.label.join('、')}}</text>
					</view>
				</view>
				<view class="wrap_info mar-t-20">
					<view class="item flex-center">
						<view class="flag bg-green">起</view>
						<text class="f-28 color-3 mar-l-10">{{item.start_address}}</text>
						<!-- <text class="f-24 color-9 mar-l-10">(中国北京市)</text> -->
					</view>
					<view class="item flex-center mar-t-15">
						<view class="flag bg-blue">终</view>
						<text class="f-28 color-3 mar-l-10">{{item.end_address}}</text>
						<!-- <text class="f-24 color-9 mar-l-10">(中国北京市)</text> -->
					</view>
					<view class="item flex-lr mar-t-20">
						<text class="f-24 color-3">{{item.depart_time}}</text>
						<text class="f-24 color-3">预估费用：{{item.price}}</text>
					</view>
				</view>
			</view>
		</view>
		<no-data tip='暂无点评' v-if="list.length==0"></no-data>
	</view>
</template>

<script>
	import noData from '../../base/nodata.vue'
	import MyApi from '../../apis/my.js'
	export default{
		name:'value',
		components:{noData},
		data(){
			return{
				list:[],
				limit:6,
				page:1,
				total:0
			}
		},
		onLoad(){
			this.init()
		},
		methods:{
			init(list=[]){
				let data = {
					page:this.page,
					limit:this.limit
				}
				MyApi.valueList(data).then(res=>{
					let _list = res.list.map(el=>{
						el.quality_grade = Math.floor(el.quality_grade)
						return el
					})
					this.list = list.concat(_list)
					this.total = res.total_page
				})
			}
		},
		onReachBottom() {
			this.page = this.page+1
			if(this.page>this.total){
				this.$util.showToast('没有更多了~')
			}else{
				this.init(this.list)
			}
		}
	}
</script>

<style lang="less" scoped>
	.avatar{
		width: 70rpx;
		height: 70rpx;
		border-radius: 50%;
		margin-right: 20rpx;
		flex-shrink: 0;
	}
	.wrap_info{
		padding: 20rpx 15rpx;
		background: #F5F5F5;
		border-radius: 12rpx;
	}
</style>
